body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/*html{
  scroll-behavior: smooth;
}*/
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Navigator{
	/*background-image: linear-gradient(to bottom left, rgba(83, 86, 91, 0.7), transparent);*/
	width: 100%;
	/*position: fixed;*/
	display: flex;
	/*margin-bottom: 20px;*/
}
#magic-click{
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-left: 1em;
	margin-top: 0.5em;
	cursor: pointer;
}
#magic-click > img{
	display: block;
	width: 50px;
	height: 50px;
	color: blue;
}
#left-menu{
	display: flex;
	flex: 1 1;
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0%;
	justify-content: flex-end;
	margin-right: 5px;
}
.Menu-Item{
	text-decoration: none;
	margin: 0px;
	padding: 10px 15px;
	font-size: 2em;
    text-transform: uppercase;
    line-height: 2;
    letter-spacing: 1px;
    color: #7bf099;
    display: block;

}
.Menu-Item:hover{
	color: red;
	border-bottom: 2px solid red;
}


.skill-bars{
	height: 90vh;
	margin-top: 5em;
  	margin-left: 10em;
  	margin-right: 10em;
}
.skill-bars h2{
  color: #7bf099;
}
.skill-container {
  width: 100%;
  background-color: #ddd;
  border-radius: 1em;
}

.skills {
  text-align: right;
  padding: 10px;
  color: white;
  border-radius: 1em;
}

.html {width: 70%; background-color: #437a25;}
.css {width: 30%; background-color: #a0d981;}
.js {width: 55%; background-color: #60ae35;}
.react {width: 60%; background-color: #59a131;}
p{
	font-size: 1.5em;
}
.particles{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.session1{
  color: #7bf099;
}
.main{
  height: 80vh;
  margin-top: 3em;
  margin-left: 10em;
  margin-right: 10em;
  text-align: center;
}
.main h1{
  margin-top: 2em;
  font-size: 5em;
  letter-spacing: 1px;
  font-weight: 400;
}
.main h2{
  font-size: 2em;
  color: #7bf099;
}
.session2{
/*  margin-top: 200px;*/
  text-align: left;
}
.session2 p{
  letter-spacing: 0.5px;
  line-height: 1.5em;
}
.session2 span{
  background-color: #aadd8e;
}
.icon-container{
  display: flex;
  justify-content: center;
  margin-top: 40px;
  padding:  5px left;
}

.icon-container img{
  width: 4em;
  height: 4em;
  margin: 0 10px;
  color: blue;
  cursor: pointer;
}
h2{
  font-size: 2em;
}
